.place__title {
  margin: 0;
  max-width: 282px;
  height: max-content;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.21;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 750px) {
  .place__title {
    font-size: 30px;
  }
}